.container {
  width: 100%;
  .section {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 393px;
    height: 100vh;
    .title {
      font-size: 12px;
      font-weight: 400;
      line-height: 15px;
      margin-bottom: 9px;
    }
    .title_date_box {
      display: flex;
      flex-direction: column;
      margin-bottom: 30px;
      .title_date {
        font-size: 40px;
        font-weight: 700;
        line-height: 40px;
      }
    }
    .sub_title_box {
      margin-bottom: 13px;
      .sub_title {
        font-size: 12px;
        font-weight: 400;
        line-height: 15px;
      }
    }
    .member_box {
      display: flex;
      gap: 29px;
      margin-bottom: 25px;
      .member_item {
        font-size: 16px;
        font-weight: 700;
        line-height: 20px;
      }
    }
    .main_img_box {
      margin: 25px 0;
      .main_img {
        width: 326px;
        height: 407px;
      }
    }
    .description_box {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 13px;
      .description_item {
        font-size: 14px;
        font-weight: 400;
        line-height: 17.5px;
      }
    }
  }
}
